import Form from '@rjsf/core'
import validator from '@rjsf/validator-ajv8'
import { JSONSchema7 } from 'json-schema'
import React from 'react'
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm'

import './Prompt.css'
import replaceAll from './utils/common'
import { getPromptById } from './utils/prompt'

function Prompt(props: any) {
  const promptFinalMetaData = getPromptById(props.id)
  const [formData, setFormData] = React.useState(null)
  const [output, setOutput] = React.useState('Output Will be display Here')

  async function onSubmit(form: any) {
    const formData = form.formData
    const schemaProperties = promptFinalMetaData.schema.properties
    const schemaForm = {} as any
    for (const [key] of Object.entries(schemaProperties)) {
      schemaForm[key] = ''
    }

    const formDataWithEmpty = { ...schemaForm, ...formData }

    const generatedPrompt = replaceAll(promptFinalMetaData.prompt, formDataWithEmpty)
    console.log({ generatedPrompt })
    // const result = await getClientResponse(generatedPrompt, 'text-davinci-003')
    const result = { response: generatedPrompt } as any
    console.log(result)
    if (result !== 'ERROR_RESPONSE') {
      setOutput(result.response as any)
    }
  }

  return (
    <>
      <div className="p-5 mb-4 bg-light rounded-3">
        <Form
          schema={promptFinalMetaData.schema as JSONSchema7}
          uiSchema={promptFinalMetaData.UiSchema}
          formData={formData}
          onChange={(e) => setFormData(e.formData)}
          validator={validator}
          onSubmit={onSubmit}
        />
        <div className="card mt-3">
          <div className="card-body">
            {<ReactMarkdown children={output} remarkPlugins={[remarkGfm]} />}
          </div>
        </div>
      </div>
    </>
  )
}

export default Prompt
