import { useState } from 'react'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import Modal from 'react-bootstrap/Modal'
import configIcon from './config.svg'

function Config(props: any) {
  const [show, setShow] = useState(false)
  const [option, setOptions] = useState(localStorage.getItem('option') as string)
  const [apiKey, setApiKey] = useState(localStorage.getItem('apiKey') as string)
  const [apiEndpoint, setApiEndpoint] = useState(localStorage.getItem('apiEndpoint') as string)

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    localStorage.setItem('apiKey', apiKey)
    localStorage.setItem('apiEndpoint', apiEndpoint)
    localStorage.setItem('option', option)
    props.onUpdate({ apiKey: apiKey, apiEndpoint: apiEndpoint })
    setShow(false)
  }
  const handleClose = () => {
    setApiKey(localStorage.getItem('apiKey') as string)
    setApiEndpoint(localStorage.getItem('apiEndpoint') as string)
    setOptions(localStorage.getItem('option') as string)
    setShow(false)
  }
  const handleShow = () => setShow(true)

  function onChangeOptions(event: any) {
    setOptions(event.target.value)
    console.log(event.target.value)
  }

  return (
    <>
      <Button className="btn" variant="" onClick={handleShow}>
        <img src={configIcon} alt="Config" className="img-responsive" />
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Form onSubmit={handleSubmit}>
          <Modal.Header closeButton>
            <Modal.Title>Configuration</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form.Label>API :</Form.Label>
            <div onChange={onChangeOptions} key={`inline-radio`} className="mb-3">
              <Form.Check
                inline
                type="radio"
                value="key"
                name="options"
                checked={option === 'key'}
              />{' '}
              Key (Browser)
              <Form.Check
                inline
                type="radio"
                value="endpoint"
                name="options"
                checked={option === 'endpoint'}
              />{' '}
              Endpoint (Server)
            </div>
            {option === 'key' && (
              <div>
                <Form.Label htmlFor="apiKey">OpenAI API Key:</Form.Label>
                <Form.Control
                  type="text"
                  id="apiKey"
                  aria-describedby="apiKeyHelpBlock"
                  value={apiKey ? apiKey : ''}
                  onChange={(e) => setApiKey(e.target.value)}
                />
                <Form.Text id="apiKeyHelpBlock" muted>
                  You can find your Secret API key in your{' '}
                  <a
                    href="https://beta.openai.com/account/api-keys"
                    target="_blank"
                    rel="noreferrer"
                  >
                    User settings{' '}
                  </a>
                  .
                </Form.Text>
              </div>
            )}
            {option === 'endpoint' && (
              <div>
                <Form.Label htmlFor="apiKey">Server Endpoint:</Form.Label>
                <Form.Control
                  type="text"
                  id="apiEndpoint"
                  aria-describedby="apiEndpoint"
                  value={apiEndpoint ? apiEndpoint : ''}
                  onChange={(e) => setApiEndpoint(e.target.value)}
                />
                <Form.Text id="apiEndpointBlock" muted>
                  You can setup server that will respond to prompt{' '}
                  <a
                    href="https://github.com/waylaidwanderer/node-chatgpt-api"
                    target="_blank"
                    rel="noreferrer"
                  >
                    Sample Repo
                  </a>
                  .
                </Form.Text>
              </div>
            )}
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button type="submit" variant="primary">
              Save
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </>
  )
}

export default Config
