import { Badge } from 'react-bootstrap'
import Table from 'react-bootstrap/Table'
import { Link } from 'react-router-dom'
import './Prompts.css'
import { getPrompts, getPromptsByCategory } from './utils/prompt'

function Prompts(props: any) {
  const badgeBg = ['primary', 'secondary', 'success', 'danger', 'warning', 'info', 'dark']
  const randomBg = function () {
    return badgeBg[Math.floor(Math.random() * badgeBg.length)]
  }
  let prompts: any = []
  if (props.category) {
    prompts = getPromptsByCategory(props.category)
  } else {
    prompts = getPrompts()
  }

  return (
    <Table striped bordered hover>
      <thead>
        <tr>
          <th>#</th>
          <th>Prompt</th>
          <th>Categories</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        {prompts.map((promptMetaData: any, index: number) => {
          return (
            <tr key={index}>
              <td>{index + 1}</td>
              <td title={promptMetaData.description}>{promptMetaData.title}</td>
              <td>
                {promptMetaData.categories.map((category: string) => {
                  return (
                    <Link to={'/category/' + category} key={category}>
                      {' '}
                      <Badge pill bg={randomBg()}>
                        {category}
                      </Badge>
                    </Link>
                  )
                })}
              </td>
              <td>
                <Link to={'/prompt/' + promptMetaData.id} className="btn btn-primary">
                  Try
                </Link>
              </td>
            </tr>
          )
        })}
      </tbody>
    </Table>
  )
}

export default Prompts
